h2 {
  color: #fff;
  padding-bottom: 100px;
  font-family: ethnocentricitalic;
  font-size: xx-large;
  display: inline-block;
}

p {
  margin: 60px 0;
  font-family: Roboto Mono;
  font-size: x-large;
  font-style: oblique;
  font-weight: bold;
  position: relative;
}

a {
  color: #f0f8ff;
  font-family: ethnocentricitalic;
}

body {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
  background-color: #000;
  background-size: cover;
  inset: 0;
}

li {
  margin: 10px 25px;
}

.center {
  background-color: #00000080;
  border: 5px;
  border-radius: .1%;
  justify-content: center;
  margin: 15% 30% 10%;
  padding: 100px;
  display: flex;
}

.row {
  flex-wrap: wrap;
  padding: 0 4px;
  display: flex;
}

.column {
  max-width: 25%;
  flex: 25%;
  padding: 0 4px;
}

.column img {
  vertical-align: middle;
  width: 100%;
  margin-top: 8px;
}

@media screen and (max-width: 800px) {
  .column {
    max-width: 50%;
    flex: 50%;
  }
}

@media screen and (max-width: 600px) {
  .column {
    max-width: 100%;
    flex: 100%;
  }
}

