h2 {
    font-family: 'ethnocentricitalic';
    font-size: xx-large;
    color: white;
    display: inline-block;

    padding-bottom: 100px;
}

p {
    font-family: 'Roboto Mono';
    font-weight: bold;
    font-style: oblique;
    position: relative;
    font-size: x-large;
    margin: 60 0vw;
}

a {
    font-family: 'ethnocentricitalic';
    color:aliceblue;
  }
  
  body {
    background-color: black;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    object-fit: cover;

    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    }
  
li {
    margin: 10px 25px;
  }
  
.center {
    border: 5px;
    border-radius: 0.1%;
    background-color: rgba(0,0,0,.5);
    display: flex;
    justify-content: center;
    padding: 100px;
    margin-top: 15%;
    margin-left: 30%;
    margin-right: 30%;
    margin-bottom: 10%;
}

.row {
    display: flex;
    flex-wrap: wrap;
    padding: 0 4px;
  }
  
  /* Create four equal columns that sits next to each other */
  .column {
    flex: 25%;
    max-width: 25%;
    padding: 0 4px;
  }
  
  .column img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
  }
  
  /* Responsive layout - makes a two column-layout instead of four columns */
  @media screen and (max-width: 800px) {
    .column {
      flex: 50%;
      max-width: 50%;
    }
  }
  
  /* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 600px) {
    .column {
      flex: 100%;
      max-width: 100%;
    }
  }